.cusTable table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: fixed;
}

.cusTable table caption {
  font-size: 1.5em;
  margin: .5em 0 .75em;
}

.cusTable table tr {
  /* background-color: #f8f8f8; */
  border: 1px solid #ddd;
  padding: .35em;
}

.cusTable table th,
.cusTable table td {
  padding: .625em;
  text-align: left;
}

.cusTable table th {
  font-size: .85em;
  letter-spacing: .1em;
  /* text-transform: uppercase; */
  padding: .625em;
  background-color: #065036;
  color: #FFF;
}
.cusTable table td {
  font-size: .8em;
}

.cusTable .mdTextAlignCenter {
  text-align: center;
}

.cusTable .mdTextAlignRight {
  text-align: right;
}



@media screen and (max-width: 768px) {

  .cusTable table {
    border: 0;
  }

  .cusTable table caption {
    font-size: 1.3em;
  }
  
  .cusTable table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  
  .cusTable table tr {
    border-bottom: 3px solid #ddd;
    display: block;
    margin-bottom: .625em;
  }
  
  .cusTable table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: .8em;
    text-align: right;
  }
  
  .cusTable table td::before {
    /*
    * aria-label has no advantage, it won't be read inside a .cusTable table
    content: attr(aria-label);
    */
    content: attr(data-label);
    float: left;
    font-weight: bold;
    /* text-transform: uppercase; */
  }
  
  .cusTable table td:last-child {
    border-bottom: 0;
    justify-items: right;
  }

  .cusTable .mdTextAlignCenter {
    text-align: right
  }
}


.inputRecChip .MuiChip-label {
  font-size: 0.8em !important;
  padding: 4px !important;
}