.dialog {
    width: 537px;
    height: 266px;
    flex-shrink: 0;
    border-radius: 20px;
    background: #C6E4D6;
}

.custom-table {
    margin: auto;
    margin-top: 20px;
    width: 500px;
    border-collapse: collapse;
}

.custom-table tbody tr:hover{
    border-radius: 6px !important;
    background: #309A34;
    color: white;
    }

