.table-container{
  overflow-x: auto;
  max-width: 100%;
}

table{
  width: 100%;
  /* border-collapse: collapse; */
}

.thead1 {
  position: relative;
  background-color: #EAECF0 !important;
  padding: 0px;
  text-align: center;
  font-size: 12px;
  font-family:Poppins;
}

.tdata1{
  position: relative;
  padding: 0px 0px;
  text-align: center;
  border: solid 1px #E7E7E7;
  font-size: 14px;
  font-family:Poppins;
}

.thead1.sticky-first-column,
.tdata1.sticky-first-column {
  position: sticky;
  /* left: 0; */
  background-color: #FFF;
  /* z-index: 1; */
  width: 40px ;
}

.thead1.sticky-second-column,
.tdata1.sticky-second-column {
  position: sticky;
  left: 0px;
  background-color: #FFF;
  /* z-index: 1; */
}

.thead1.fixed-last-column,
.tdata1.fixed-last-column {
  position: sticky;
  right: 0;
  background-color: #FFF;
  z-index: 2;
  width: 70px ;
  height:auto;
}

.thead1.fixed-width,
.tdata1.fixed-width {
  width: 220px ;
}


