.tdata1 input{
    border: none;
    padding: 0;
    margin: 0;
    width: 55px;
    height: 100%;
    text-align: center;
    box-sizing: border-box;
    background-color: transparent;

    appearance: textfield;
    -moz-appearance: textfield;
    ::-webkit-inner-spin-button,
    ::-webkit-outer-spin-button{
        margin: 0;
    }
}

.pagination-container {
  display: flex;
  padding: 10px;
  align-items: flex-start;
  gap: 10px;
  border-radius: 4px;
  background: #FFF;
  margin-top: -1.5%;
  margin-left: 80%;
}

.page-button {
  display: flex;
  padding: 10px;
  align-items: flex-start;
  gap: 10px;
  border: 1px solid #B3B3B3;
  border-radius: 4px;
  cursor: pointer;
  background: #FFF;
}

.page-button.active {
  display: flex;
  padding: 10px;
  align-items: flex-start;
  gap: 10px;
  border-radius: 4px;
  border: 1px solid #000;
  background: #141212;
  color: #FFF; /* Text color when active */
}

.download {
  background: #000000;
  color: #ffffff;
  height: 41px;
  border-radius: 5px;
  margin: 5px;
  padding: 0px 10px;
  cursor: pointer;
  transition: box-shadow 0.3s, background 0.3s, color 0.3s;   
}

.download:hover {
  background: #5f5c5c;
  color: #ffffff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); 
}



